@use "../utils/" as *;
@use "../node_modules/sass-rem" as rem;

.footer {
  margin-top: 4.5rem;
  border-top-right-radius: 100px;
  background: var(--footer-background);
  font-family: var(--font-ubuntu);
  text-align: center;

  @include breakpoint-up(medium) {
    display: flex;
    padding: 3rem;
  }

  @include breakpoint-up(large) {
    margin-top: 6rem;
    padding: 1.5rem 10rem;
    text-align: left;
  }

  &__logo {
    padding: 3rem;

    &:hover {
      cursor: pointer;
    }

    > img {
      margin: 0 auto;
    }
  }

  &__wrapper {
    @include breakpoint-up(medium) {
      flex: 2 1 auto;
      display: flex;
      padding: 3rem;
      justify-content: space-between;
    }

    @include breakpoint-up(large) {
      justify-content: space-around;
      padding: 3.5rem;
    }
  }

  &__item {
    color: var(--white-text);
    font-weight: 500;
  }

  &__links {
    color: var(--footer-text);
    font-weight: 400;
    line-height: 2.5rem;

    > li:first-child {
      margin-top: 1rem;

      @include breakpoint-up(large) {
        margin-top: 2rem;
      }
    }
  }
}
